'use client';

import { useState } from 'react';
import { Modal } from './ui/modal';
import { Input } from './ui/input';
import { Button } from './ui/button';
import { checkManualSync } from '@/lib/api';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { ChatwootContact } from '@/lib/types';

interface CreateLeadModalProps {
  isOpen: boolean;
  onClose: () => void;
  contact: ChatwootContact;
}

export default function CreateLeadModal({ isOpen, onClose, contact }: CreateLeadModalProps) {
  const [name, setName] = useState(contact.name || '');
  const [phone, setPhone] = useState(contact.phone_number || '');
  const [email, setEmail] = useState(contact.email || '');
  const queryClient = useQueryClient();

  const mutation = useMutation({
    mutationFn: checkManualSync,
    onSuccess: (data) => {
      // Data likely contains { lead_id, status, message }
      // We should invalidate the main lead query to reload the dashboard with the new/linked lead
      queryClient.invalidateQueries({ queryKey: ['lead'] }); // Invalidate generic or specific
      // Ideally we pass the new lead ID up or reload context. 
      // Assuming the main page queries based on Chatwoot ID, re-fetching that will now find the lead.
      onClose();
      alert(`Succes: ${data.status === 'created' ? 'Nieuwe lead aangemaakt' : 'Gekoppeld aan bestaande lead'}`);
    },
    onError: () => {
      alert("Fout bij synchroniseren.");
    }
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    mutation.mutate({
      name,
      phone,
      email,
      chatwoot_id: contact.id
    });
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose} title="Nieuwe Lead / Koppelen">
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="space-y-2">
          <label className="text-sm font-medium">Naam</label>
          <Input value={name} onChange={e => setName(e.target.value)} required />
        </div>
        <div className="space-y-2">
          <label className="text-sm font-medium">Telefoon</label>
          <Input value={phone} onChange={e => setPhone(e.target.value)} required />
        </div>
        <div className="space-y-2">
          <label className="text-sm font-medium">Email</label>
          <Input value={email} onChange={e => setEmail(e.target.value)} type="email" />
        </div>
        
        <div className="pt-4 flex justify-end space-x-2">
          <Button type="button" variant="outline" onClick={onClose}>Annuleren</Button>
          <Button type="submit" isLoading={mutation.isPending}>
            Sync naar Odoo
          </Button>
        </div>
      </form>
    </Modal>
  );
}

