'use client';

import { useState, useEffect } from 'react';
import { Lead } from '@/lib/types';
import { updateLeadField } from '@/lib/api';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { Input } from './ui/input';
import { Loader2, Check } from 'lucide-react';
import { cn } from '@/lib/utils';

// Simple Label component inline for now
const Label = ({ className, children, ...props }: React.HTMLAttributes<HTMLLabelElement>) => (
  <label className={cn("text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70", className)} {...props}>{children}</label>
);

interface LeadDetailsProps {
  lead: Lead;
}

export default function LeadDetails({ lead }: LeadDetailsProps) {
  const queryClient = useQueryClient();

  const { mutate, isPending, variables } = useMutation({
    mutationFn: async ({ field, value }: { field: string; value: string }) => {
      return await updateLeadField(lead.id, field, value);
    },
    onSuccess: () => {
       // Invalidate query to refetch fresh data
       queryClient.invalidateQueries({ queryKey: ['lead', lead.id] });
    },
    onError: (error) => {
      console.error("Failed to update", error);
      alert("Update failed"); // Replace with toast later
    }
  });

  // Helper to render editable field
  const renderField = (label: string, fieldKey: keyof Lead | string, value: string | undefined, type: string = "text") => {
    const [localValue, setLocalValue] = useState(value || '');
    const [isDirty, setIsDirty] = useState(false);

    useEffect(() => {
        setLocalValue(value || '');
    }, [value]);

    const handleBlur = () => {
      if (localValue !== (value || '')) {
        mutate({ field: fieldKey as string, value: localValue });
        setIsDirty(false);
      }
    };

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setLocalValue(e.target.value);
        setIsDirty(true);
    };

    const isUpdating = isPending && variables?.field === fieldKey;

    return (
      <div className="grid gap-2">
        <Label>{label}</Label>
        <div className="relative">
          <Input 
            type={type}
            value={localValue} 
            onChange={handleChange}
            onBlur={handleBlur}
            disabled={isUpdating}
            className={cn(isUpdating && "opacity-50")}
          />
          {isUpdating && <Loader2 className="absolute right-3 top-2.5 h-4 w-4 animate-spin text-muted-foreground" />}
          {!isUpdating && isDirty && <div className="absolute right-3 top-2.5 h-2 w-2 rounded-full bg-yellow-500" title="Unsaved" />}
        </div>
      </div>
    );
  };

  return (
    <div className="grid gap-4 py-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {renderField("Naam", "contact_name", lead.contact_name || lead.name)}
        {renderField("Telefoon", "phone", lead.phone, "tel")}
        {renderField("Email", "email_from", lead.email_from, "email")}
        {renderField("Voertuig", "x_studio_voertuig", lead.x_studio_voertuig)}
        {renderField("Velgmodel", "x_studio_velgmodel", lead.x_studio_velgmodel)}
        {renderField("Inchmaat", "x_studio_inchmaat", lead.x_studio_inchmaat)}
        {renderField("Kleur", "x_studio_kleur", lead.x_studio_kleur)}
      </div>
      <div className="text-xs text-muted-foreground mt-2">
        Wijzigingen worden automatisch opgeslagen.
      </div>
    </div>
  );
}

