'use client';

import { Lead, PipelineStage, STAGES } from '@/lib/types';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { updateLeadStage, updateLeadField } from '@/lib/api';
import { cn } from '@/lib/utils';
import { Check, Loader2 } from 'lucide-react';
import { useState, useEffect } from 'react';

interface LeadPipelineProps {
  lead: Lead;
}

export default function LeadPipeline({ lead }: LeadPipelineProps) {
  const queryClient = useQueryClient();
  const currentStageId = lead.stage_id ? lead.stage_id[0] : 0;

  const stageMutation = useMutation({
    mutationFn: ({ stageId }: { stageId: number }) => updateLeadStage(lead.id, stageId),
    onSuccess: () => queryClient.invalidateQueries({ queryKey: ['lead', lead.id] }),
  });

  const probMutation = useMutation({
    mutationFn: ({ prob }: { prob: number }) => updateLeadField(lead.id, 'probability', prob),
    onSuccess: () => queryClient.invalidateQueries({ queryKey: ['lead', lead.id] }),
  });

  const [probability, setProbability] = useState(lead.probability || 0);

  useEffect(() => {
      setProbability(lead.probability || 0);
  }, [lead.probability]);

  const handleStageClick = (stageId: number) => {
    if (stageId === currentStageId) return;
    stageMutation.mutate({ stageId });
  };

  const handleProbChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setProbability(Number(e.target.value));
  };

  const handleProbCommit = () => {
    if (probability !== lead.probability) {
        probMutation.mutate({ prob: probability });
    }
  };

  return (
    <div className="space-y-6 py-4">
      {/* Pipeline Stepper */}
      <div className="relative flex items-center justify-between w-full">
        <div className="absolute top-1/2 left-0 w-full h-1 bg-gray-200 -z-10 transform -translate-y-1/2" />
        
        {STAGES.map((stage, index) => {
          const isActive = currentStageId === stage.id;
          const isCompleted = STAGES.findIndex(s => s.id === currentStageId) > index;
          const isPending = stageMutation.isPending && stageMutation.variables?.stageId === stage.id;

          return (
            <button
              key={stage.id}
              onClick={() => handleStageClick(stage.id)}
              disabled={stageMutation.isPending}
              className={cn(
                "flex flex-col items-center group focus:outline-none",
                (isActive || isCompleted) ? "text-blue-600" : "text-gray-500"
              )}
            >
              <div className={cn(
                "w-8 h-8 rounded-full flex items-center justify-center border-2 transition-colors bg-white",
                isActive ? "border-blue-600 bg-blue-50" : 
                isCompleted ? "border-blue-600 bg-blue-600 text-white" : 
                "border-gray-300 group-hover:border-gray-400"
              )}>
                {isPending ? <Loader2 className="w-4 h-4 animate-spin" /> : 
                 isCompleted ? <Check className="w-4 h-4" /> : 
                 <span className="text-xs font-bold">{index + 1}</span>}
              </div>
              <span className="mt-2 text-xs font-medium">{stage.name}</span>
            </button>
          );
        })}
      </div>

      {/* Probability Slider */}
      <div className="space-y-2">
        <div className="flex justify-between text-sm">
          <span className="font-medium">Slagingskans</span>
          <span className="font-bold">{probability}%</span>
        </div>
        <input
          type="range"
          min="0"
          max="100"
          step="10"
          value={probability}
          onChange={handleProbChange}
          onMouseUp={handleProbCommit}
          onTouchEnd={handleProbCommit}
          className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer accent-blue-600"
          disabled={probMutation.isPending}
        />
      </div>
    </div>
  );
}

